.grid {
    --amount: 24;
    --counter: 1;
    position: relative;
    padding: 0;
    margin: 0;
    list-style-type: none;
    display: grid;
    grid-template-columns: repeat(var(--amount), 1fr 2fr) 1fr;
    grid-gap: 0.5em 1em;
    width: 3274px;
    height: 3826px;
    /*transform: translate(-50px , -100px);*/
}

@media only screen and (max-width: 640px) {
    .grid {
        grid-gap: 0.1rem 0.2rem;
    }
}

.wrapper {
    overflow: visible;
}

body {
    overflow: hidden;
}

.board {
    position: relative;
    padding-top: 50px;
    height: 100vh;
    width: calc(100vw - 250px);
    overflow: hidden;
}

@media only screen and (max-width: 768px) {
   .board {
       width: calc(100vw - 100px);
   }
}

.wrapper {
    position: relative;
    overflow: scroll;
    height: calc(100vh - 30px);
    width: calc(100vw - 250px);
}

@media only screen and (max-width: 768px) {
   .wrapper {
       width: calc(100vw - 100px);
   }
}

.border {
    position: absolute;
    --amount: 25;
    --counter: 1;
    margin: 0;
    list-style-type: none;
    display: grid;
    grid-template-columns: repeat(var(--amount), 1fr 2fr) 1fr;
    grid-gap: 0.1rem 0.2rem;
    width: 3410px;
    transform: translate( 0 , 50%);
    max-height: 150px;
    overflow: hidden;
}

@media only screen and (min-width: 768px) {
   .border {
       grid-gap: 0.5em 1em;
   }
}

.bottom {
    left: 0;
    right: 0;
    bottom: -4px;
}

.left , .right{
    width: 169px;
    left: 0;
    top: 0;
    bottom: 0;
    display: block;
    position: absolute;
    transform: translate(-80% , -78px);
}

.right {
    left: auto;
    right: -136px;
    top: -157px;
    transform: none;
}

.top {
    top: 2px;
    left: 0;
    right: 0;
    transform: rotate(180deg);
}

@media only screen and (max-width: 768px) {
   .bottom {
       bottom: 11px;
   }

    .left {
        transform: translate(-76% , -80px);
    }

    .left , .right {
        width: 177.39px;
    }

    .right {
        right: -135px;
        top: -158px;
    }

    .top {
        top: 11px;
    }
}

.grid.auto {
    cursor: auto;
}

.grid.pointer {
    cursor: pointer;
}

.grid.cell {
    cursor: cell;
}

.hex {
    --counter : 1;
    grid-row: calc(var(--counter) + var(--counter))/span 2;
    filter: drop-shadow(0 0 10px rgba(68, 68, 68, 0.08));
    height: 0;
    padding-bottom: 90%;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
    transform: scale(1);
}

.hex:nth-of-type(odd){
    grid-row: calc(var(--counter) + var(--counter) - 1)/span 2;
}

.box::after {
    content: "";
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    clip-path: polygon(75% 0, 100% 50%, 75% 100%, 25% 100%, 0 50%, 25% 0);
    background-size: cover;
    background-image: url(../../../assets/hex-border.png);
    z-index: 1;
}

.left .hex , .right .hex{
    margin-bottom: 8px;
}

@media only screen and (max-width: 768px) {
    .left .hex , .right .hex{
        margin-bottom: 0.09em;
    }
}

.overlay {
    position: absolute;
    height: 99%;
    width: 99%;
    left: 0;
    top: 0;
    clip-path: polygon(75% 0, 100% 50%, 75% 100%, 25% 100%, 0 50%, 25% 0);
    background-size: cover;
    background: #ECEFF9;
    z-index: 0;
    pointer-events: none;
}

.borderWrapTop {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    overflow: hidden;
    height: 150px;
    z-index: -1;
}

.borderWrapBottom {
    position: absolute;
    bottom: -4px;
    left: 0;
    right: 0;
    overflow: hidden;
    height: 150px;
    z-index: -1;
    transform: translateY(62%);
}

@media only screen and (max-width: 768px) {
   .borderWrapBottom {
       transform: translateY(85%);
   }
}

.borderWrapLeft , .borderWrapRight {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    overflow: hidden;
    width: 169px;
    z-index: -1;
    height: calc(100% + 75px);
}

.borderWrapRight {
    left: auto;
    right: 0;
}

.popup {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0 , 0, 0, 0.4);
}
