.hexagon {
    clip-path: polygon(75% 0, 100% 50%, 75% 100%, 25% 100%, 0 50%, 25% 0);
    position: relative;
    padding-bottom: 89%;
}

.popup {
    position: absolute;
    left: 50%;
    top: 50%;
    background-color: #fff;
    width: 300px;
    transform: translate(-25px , -50%);
    padding-bottom: 250px;
}

.box {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    text-align: center;
    padding: 50px 25%;
    background-color: transparent;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.box:after {
    content: "";
    position: absolute;
    height: 97%;
    width: 100%;
    left: 0;
    top: 5px;
    background-size: contain;
    background-image: url(../../../assets/hexagon-login.png);
    z-index: -1;
}

.popup .box:after {
    display: none;
}