.navbar {
    position: fixed;
   right: 0;
    top: 0;
    height: 50px;
    width: calc(100vw - 100px);
    border-right: solid 1px lightgray;

    background: #f5f5f5;
    box-sizing: border-box;
    z-index: 3;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}
@media screen and (min-width: 575px) {
    .navbar {
        width: calc(100vw - 200px);
        padding: 0 15px;
    }
}
@media screen and (min-width: 840px) {
    .navbar {
        width: calc(100vw - 250px);
        padding: 0 25px;
    }
}

.content {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    padding: 5px 10px;
    text-overflow: ellipsis;
    overflow: hidden;
    font-size: 24px;
    text-align: center;
    box-sizing: border-box;
}

.navbar [contenteditable=true]:empty:before{
    content: 'Enter your question here.';
    pointer-events: none;
    display: block; /* For Firefox */
}